<template>
    <div>
        <span>{{formValidate}}</span>
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
            <FormItem label="Name" prop="name">
                <Input v-model="formValidate.name" placeholder="Enter your name"></Input>
            </FormItem>
            <FormItem label="数字" prop="people">
                <Input v-model="formValidate.people"  placeholder="Enter your name"></Input>
            </FormItem>
            <FormItem label="时间" prop="date">
                <DatePicker type="datetime" placeholder="Select date" v-model="formValidate.date"></DatePicker>
            </FormItem>
            <FormItem>
                <Button type="primary" @click="handleSubmit('formValidate')">Submit</Button>
                <Button @click="handleReset('formValidate')" style="margin-left: 8px">Reset</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
import dictionaryList from '@/mixins/dictionaryList'
import vld from '@/libs/validateRules'
export default {
    mixins: [dictionaryList],
    name: '',
    data() {
        return {
            formModel: {},
            objlist: [
                { title: '通知人名称：', name: 'attenName' },
                { title: '代码：', name: 'code' },
                { title: '地址：', name: 'attenAddr' },
                { title: '电话：', name: 'attenTel' },
                { title: '传真：', name: 'attenFax' },
                { title: '邮箱：', name: 'attenEmail' },
                {},
            ],
            obj: {
                attenAddr: '重庆市南川区2',
                attenEmail: '8436315263@163.com',
                attenFax: '788544-4452',
                attenName: '小蓝',
                attenTel: '12223546642',
                code: '1456687552',
                contactCode: 'NP202007251119104484',
                entrustCode: 'LT202007251119090608',
                executoryEntrustCode: 'LTC202007251119097719',
                linkType: '通知人',
            },
            formValidate: {
                name: '',
                mail: '',
                city: '',
                gender: '',
                interest: [],
                date: '',
                time: '',
                desc: '',
            },
            ruleValidate: {
                name: [
                    {
                        required: true,
                        message: 'The nam好女孩试试看e cannot be empty',
                        trigger: 'blur',
                    },
                ],
                people: [
                    {
                        required: true,
                        message: '请输入扫码解锁人数',
                        trigger: 'blur',
                    },
                    {
                        type: 'number',
                        message: '请输入数字格式',
                        trigger: 'blur',
                        transform(value) {
                            return Number(value)
                        },
                    },
                ],
                mail: [
                    {
                        required: true,
                        message: 'Mailbox cannot be empty',
                        trigger: 'blur',
                    },
                    {
                        type: 'email',
                        message: 'Incorrect email format',
                        trigger: 'blur',
                    },
                ],
                city: [
                    {
                        required: true,
                        message: 'Please select the city',
                        trigger: 'change',
                    },
                ],
                gender: [
                    {
                        required: true,
                        message: 'Please select gender',
                        trigger: 'change',
                    },
                ],
                interest: [
                    {
                        required: true,
                        type: 'array',
                        min: 1,
                        message: 'Choose at least one hobby',
                        trigger: 'change',
                    },
                    {
                        type: 'array',
                        max: 2,
                        message: 'Choose two hobbies at best',
                        trigger: 'change',
                    },
                ],
                date: [
                    // {
                    //     required: true,
                    //     type: 'daterange',
                    //     message: 'Please select the date',
                    //     trigger: 'change'
                    // }
                    {
                        required: true,
                        validator: vld.validateTime,
                        trigger: 'change',
                    },
                ],
                time: [
                    {
                        required: true,
                        type: 'string',
                        message: 'Please select time',
                        trigger: 'change',
                    },
                ],
                desc: [
                    {
                        required: true,
                        message: 'Please enter a personal introduction',
                        trigger: 'blur',
                    },
                    {
                        type: 'string',
                        min: 20,
                        message: 'Introduce no less than 20 words',
                        trigger: 'blur',
                    },
                ],
            },
        }
    },
    created() {},
    mounted() {},
    methods: {
        handleSubmit(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.$Message.success('Success!')
                } else {
                    this.$Message.error('Fail!')
                }
            })
        },
        handleReset(name) {
            this.$refs[name].resetFields()
        },
    },
}
</script>

<style scoped lang="scss">
.item-form-title {
    height: 35px;
    font-size: 16px;
    font-weight: bold;
    color: rgba(51, 51, 51, 1);
    line-height: 35px;
}
.table-boxjz {
    box-sizing: border-box;
    border: 1px solid #e6e6e6;
    .table-rowjz {
        height: 30px;
        line-height: 30px;
        display: flex;
        border-bottom: 1px solid #e6e6e6;
        div {
            font-size: 14px;
            height: 100%;
            font-weight: bold;
            color: rgba(102, 102, 102, 1);

            &:first-child {
                width: 120px;
                text-align: right;
                background: #f5f6f8;
                border-right: 1px solid #e6e6e6;
            }
            &:last-child {
                flex: 1;
                background: #fff;
                padding-left: 8px;
            }
        }
    }
}
</style>
